.recipe{
    background-image: linear-gradient(45deg, #874da2 0%, #c43a30 100%);
 margin-top:10px;
 box-shadow: 0 5px 20px rgb(71, 64, 64);
 color:white;
 padding:10px;
 display:flex;
 flex-direction:column;
 align-items:center;
 justify-content:space-between;
 width: 30%;
 border-radius: 10px;
 background-size: cover;
 background-position: center;
}
.ingredients{
    list-style: none;
    font-size:1.3rem;
}
.image{
    border-radius:50%;
    box-shadow: 0 0 10px white;
}
@media(max-width:739px){
    .recipe{
        width:90%;
    }
}