.App{
  background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%);
}

.search_wrap{
  display:flex;
  justify-content:center;
  padding:10px 0 20px;

}

.input_search{
  padding:10px;
  border-radius: 3px;
  border: 1px solid rgb(212, 212, 212);
  background-color:#fffbfd;
  width:50%;
  font-size:1.8rem;
}
.search_btn{
  padding:10px;
  font-size:1.8rem;
  margin-left: 10px;
  border: none;
  color:white;
  border-radius: 3px;
  background-color: #f5576c;
  font-weight: 600;
}
.search_btn:active{
  filter: brightness(0.8);
}
.suggestion{
  color:#d82941;
  text-shadow: 0 1px 1px black;
  text-align:center;
  font-size:3rem;
  margin:10px 0 20px;
}
.body_wrap{
  display:flex;
  flex-wrap: wrap;
  justify-content: space-around;
}